.m-opacity {
    color: #8D8E8E;
}

/*display*/

.m-inline-block {
    display: inline-block !important;
}

/**/

.m-right-top {
    position:  absolute;
    top: 0;
    right: 0;
}

.ui.comments {
    max-width: 98%;
}

/**/

.m-shadow-small {
    -webkit-box-shadow: 0 4px 8px rgba(0,0,0,0.2) !important;
    box-shadow: 0 4px 8px rgba(0,0,0,0.2) !important;
}

.m-mobile-show {
    display: none !important;
}

.admin {
    width: 70px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    border-radius: 5px;
    background-color: #1B1C1D;
    color: #1B1C1D;
    cursor: pointer;
    float: left;
}

.admin:hover {
    background-color: #6A6A6A;
    color: black;
}

/*  */

@media screen and (max-width: 768px){
    .m-mobile-hidden {
        display: none !important;
    }
    .m-mobile-show {
        display: block !important;
    }
    .m-padded-lr-responsive {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }
    .m-mobile-wide {
        width: 100% !important;
    }
    .m-mobile-lr-clear {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }
}

.m-inline-block {
    display: inline-block !important;
}

.m-container {
    max-width: 72em !important;
    margin: auto !important;
}

.m-container-small2 {
    max-width: 70em !important;
    margin: auto !important;
}

.m-container-small3 {
    max-width: 65em !important;
    margin: auto !important;
}

.m-container-small {
    max-width: 60em !important;
    margin: auto !important;
}

/**/

.m-text {
    font-weight: 300 !important;
    letter-spacing: 0.5px !important;
    line-height: 1.7;
}

.m-black {
    color: #333 !important;
}

.js-toc {
    padding-left: 0px;
}

.ui.popup {
    padding-left: 0.5em;
}

.m-min-height {
    min-height: 550px;
}

